@media (max-width: 768px) {
  .amotos-my-invoices table {

    tr{
      td:not(.view):before{
        float: right;
      }
      td {
        text-align: left !important;
      }
    }
    tbody {
      td,
      th {
        padding-right: 10px;
        padding-left: 0;
      }
    }
  }
}